@import '../../../../../themes/src/default.css';
@import '../../../vars.css';

:root {
    --select-mobile-footer-border-top: 1px solid var(--color-light-border-primary);
    --select-mobile-footer-background-color: var(--color-light-bg-primary);
}

.virtualOptionsList {
    width: 100%;
    outline: none;
    box-sizing: border-box;
    position: relative;
}

.inner {
    position: relative;
    width: 100%;
}

.virtualRow {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;

    &:before {
        content: '';
        position: absolute;
        z-index: 1;
        left: var(--select-option-left-padding);
        right: var(--select-option-right-padding);
        top: 0;
        height: 1px;
        background: var(--select-option-divider-background);
        display: var(--select-option-divider-display);
    }

    &:first-child:before {
        display: none;
    }
}

.highlighted {
    &:before,
    & + .virtualRow:before {
        display: none;
    }
}

.emptyPlaceholder {
    padding: var(--gap-m) var(--gap-s);
    color: var(--select-options-list-empty-placeholder-color);
}

.l .emptyPlaceholder,
.xl .emptyPlaceholder {
    padding: var(--gap-xl) var(--gap-m);
}

.footer {
    display: flex;
    justify-content: space-between;
    position: sticky;
    padding: var(--gap-m) var(--gap-m);
    background-color: var(--color-light-bg-primary);
    bottom: 0;

    & .footerButton:first-of-type {
        margin-right: var(--gap-m);
    }
}

.footerButton {
    width: 50%;
}

.footerHighlighted {
    border-top: var(--select-mobile-footer-border-top);
    background-color: var(--select-mobile-footer-background-color);
}
